.box  span{
  width: 100%;
  background-color: #6797be;
  border-radius: 4px;
  color: white;
  padding: 12px;
  width: 250px;
  border: none;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
  -webkit-appearance: button;
  appearance: button;
  outline: none;
}

  .box select{
    width: 100%;
    background-color: #6797be;
    border-radius: 4px;
    color: white;
    padding: 12px;
    width: 250px;
    border: none;
    box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
    -webkit-appearance: button;
    appearance: button;
    outline: none;
  }
  
