  .box:hover::before {
    color: rgba(255, 255, 255, 0.6);
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  .box select option {
    padding: 30px;
  }
  .search-icon span {
    width:22px;
    height:22px;
    display: inline-block;
    vertical-align: middle;
    position:relative;
    transform: rotate(45deg);
    transition: all .4s cubic-bezier(0.650, -0.600, 0.240, 1.650);
}
  .icon {
    min-width: 46px;
    min-height: 46px;
    display: flex;
    /* align-items: center; */
    /* justify-content: center; */
    margin-right: 12px;
    box-shadow: 
      -2px -2px 6px rgba(rgb(148, 224, 247), .6),
      2px 2px 12px #fcf270;
      
    
  }
 .icon svg {
    transform: translate(-1px, -1px);    
  }